/*
    There are two type of tiles "*-lg and *-sm"
    The large tile has text aligned to right, and diffirent size
    The small tile has texst and title center with smaller buttons
    MOBILE FIRST
*/
.cmg-entree-tile {
  position: relative;
  opacity: 1; }

.cmg-entree-tile:hover {
  cursor: pointer;
  opacity: .9; }

.cmg-entree-title {
  font-family: "TradeGothicLTCom Bold";
  position: absolute;
  right: 0;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  color: white;
  font-weight: bold; }

.cmg-entree-tile-lg .cmg-entree-title {
  text-align: right;
  font-size: 32px;
  padding: 0 20px 5px 0;
  bottom: 60px; }

.cmg-entree-tile-sm .cmg-entree-title {
  text-align: center;
  font-size: 18px;
  padding: 0 0 5px 0;
  bottom: 50px; }

.cmg-entree-tile img {
  width: 100%;
  margin: 0; }

/* Text container section */
.cmg-entree-tile-lg .select-button-box {
  background-color: orange;
  position: absolute;
  bottom: 20px;
  right: 20px;
  text-align: right;
  padding: 5px 20px; }

.cmg-entree-tile-sm .select-button-box {
  background-color: orange;
  position: absolute;
  bottom: 20px;
  right: 0;
  left: 0;
  margin-left: auto;
  margin-right: auto;
  max-width: 75px;
  text-align: right;
  padding: 5px 6px; }

/*Text styles*/
.select-button-text {
  color: white;
  font-family: "TradeGothicLTCom Bold"; }

.cmg-entree-tile-lg .select-button-text {
  font-size: 24px;
  text-align: center; }

.cmg-entree-tile-sm .select-button-text {
  font-size: 14px;
  text-align: center; }

/*Media*/
@media (min-width: 768px) {
  .cmg-entree-title {
    font-size: 24px; }
  .select-button-text {
    font-size: 18px; }
  .select-button-box {
    padding: 5px 10px; }
  .cmg-entree-tile-sm .cmg-entree-title {
